*,
::after,
::before {
  box-sizing: border-box;
}

html,
body {
  font-size: 14px;
}

@media only screen and (min-width: 1224px) {
  html,
  body {
    font-size: 18px;
  }
}

body {
  font-family: "Roboto", "Open Sans", sans-serif;
  min-height: 100vh;
  min-width: 320px;
  color: #383838;
}

a,
button {
  outline: none;
}

a:hover,
button:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
  transition: .2s ease;
  color: inherit;
}

a:hover {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child, p:last-child {
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: inherit;
}

ul, ol, dl {
  margin: 0;
  padding: 0;
  list-style: none;
}

ul li, ol li, dl li {
  margin-bottom: 10px;
}

ul li:last-child, ol li:last-child, dl li:last-child {
  margin-bottom: 0;
}

.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}

.no-scroll {
  overflow: hidden;
}

.phone-list-title:hover,
.address-list-title:hover,
.call-back:hover,
.catalog-btn:hover {
  cursor: pointer;
}

.wrap:first-child p,
.info,
.cart p,
.catalog p {
  display: none;
}

.header-item {
  border-bottom: 1px solid #bdbdbd;
}

.header-item .container {
  display: flex;
  justify-content: space-between;
}

.header-item:first-child {
  font-size: 1rem;
}

.header-item:first-child .container {
  padding-top: 14px;
  padding-bottom: 9px;
}

.header-item:first-child .auth {
  position: relative;
  padding-left: 24px;
}

.header-item:first-child .auth:before {
  content: url("../img/login.png");
  position: absolute;
  left: 0;
  top: 0;
}

.mob-burger-menu {
  font-size: 0rem;
  position: relative;
  width: 25px;
}

.mob-burger-menu span {
  width: 100%;
  height: 2px;
  background: #383838;
  display: block;
  margin-bottom: 5px;
}

.mob-burger-menu span:last-child {
  margin-bottom: 0;
}

@media only screen and (min-width: 1024px) {
  .mob-burger-menu {
    display: none;
  }
}

.navigation {
  position: absolute;
  top: 40px;
  left: 0;
  padding: 20px;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
}

.navigation li {
  margin-bottom: 15px;
  padding-left: 12px;
  position: relative;
}

.navigation li:last-child {
  margin-bottom: 0;
}

.navigation li:before {
  content: "";
  position: absolute;
  top: 3px;
  left: -4px;
  width: 8px;
  height: 8px;
  border-right: 1px solid #8ecf04;
  border-bottom: 1px solid #8ecf04;
  transform: rotate(-45deg);
}

@media only screen and (min-width: 1024px) {
  .navigation {
    display: flex;
    position: static;
    padding: 0;
    width: auto;
    visibility: visible;
    opacity: 1;
  }
  .navigation li {
    margin-bottom: 0;
    margin-right: 35px;
  }
  .navigation li:last-child {
    margin-right: 0;
  }
}

@media only screen and (min-width: 1224px) {
  .navigation li:before {
    top: 5px;
    width: 10px;
    height: 10px;
  }
}

@media only screen and (min-width: 500px) {
  .wrap {
    display: flex;
    align-items: center;
  }
}

@media only screen and (min-width: 1024px) {
  .wrap:first-child p {
    font-size: 0.77778rem;
    display: block;
    padding: 9px 13px;
    max-width: 206px;
    border-left: 1px solid #bdbdbd;
  }
}

.logo {
  margin: 5px 0 5px -5px;
  max-width: 140px;
}

@media only screen and (min-width: 768px) {
  .logo {
    max-width: 215px;
  }
}

@media only screen and (min-width: 1224px) {
  .logo {
    max-width: none;
    margin: 16px 0 16px -10px;
  }
}

@media only screen and (min-width: 500px) {
  .info {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 10px 0;
    padding-right: 15px;
  }
}

@media only screen and (min-width: 768px) {
  .info {
    padding-right: 50px;
  }
}

.work-time {
  font-size: 0.66667rem;
  color: #a9a9a9;
}

.work-time:before {
  content: url("../img/clock.png");
  margin-right: 4px;
  position: relative;
  top: 2px;
}

.phone-link {
  font-size: 1.33333rem;
  display: inline-block;
  margin: 5px 0;
  color: #70a000;
}

.phone-link:hover {
  text-decoration: none;
}

.phone-list-title:after,
.address-list-title:after {
  content: "";
  position: absolute;
  right: -16px;
  width: 8px;
  height: 8px;
  border-right: 1px solid #8ecf04;
  border-bottom: 1px solid #8ecf04;
  transform: rotate(45deg);
}

.phone-list-title {
  font-size: 0.77778rem;
  position: relative;
  margin-right: 18px;
  color: #a9a9a9;
  border-bottom: 1px dashed;
}

.phone-list-title .info-list {
  font-size: 1.11111rem;
  min-width: 160px;
  text-align: center;
  color: #70a000;
}

@media only screen and (min-width: 1224px) {
  .phone-list-title:after {
    top: 3px;
  }
}

.info-list {
  padding: 10px;
  min-width: 220px;
  position: absolute;
  top: 20px;
  right: -20px;
  background: #fff;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.5);
  transition: .2s ease;
  opacity: 0;
  z-index: 1;
  visibility: hidden;
}

.info-address {
  font-size: 0.88889rem;
  font-weight: 300;
  color: #a9a9a9;
}

.address-list-title,
.call-back {
  position: relative;
  border-bottom: 1px dashed;
  margin-left: 18px;
}

.address-list-title:before,
.call-back:before {
  content: '';
  position: absolute;
  left: -16px;
  top: -2px;
}

.address-list-title {
  margin-bottom: 8px;
  margin-right: 20px;
}

.address-list-title:before {
  content: url("../img/map-marker.png");
}

.address-list-title:after {
  top: 2px;
}

@media only screen and (min-width: 1224px) {
  .address-list-title:after {
    top: 4px;
  }
}

.call-back:before {
  content: url(../img/phone.png);
  top: 2px;
}

.cart {
  font-size: 0.88889rem;
  position: relative;
  height: 100%;
  color: #fff;
  background: url("../img/cart.png") no-repeat 35% 50% #8ecf04;
  background-size: 80% 60%;
  padding-left: 50px;
}

.cart span {
  font-size: 0.94444rem;
  position: absolute;
  top: 6px;
  left: 25px;
}

.cart .price {
  font-size: 0.66667rem;
  font-weight: 300;
  margin-top: 5px;
}

@media only screen and (min-width: 500px) {
  .cart {
    height: 70%;
    background-size: 80% 50%;
  }
  .cart span {
    top: 10px;
  }
}

@media only screen and (min-width: 768px) {
  .cart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 15px;
    padding-left: 80px;
    height: 100%;
    background-position: 15% 50%;
    background-size: 35% 40%;
  }
  .cart span {
    top: 20px;
    left: 42px;
  }
  .cart p {
    display: block;
  }
}

@media only screen and (min-width: 1224px) {
  .cart span {
    top: 30px;
    left: 44px;
  }
}

.catalog {
  position: relative;
  background: #f76c06;
  color: #fff;
}

.catalog:before, .catalog:after {
  content: '';
  width: 100%;
  position: absolute;
  border-top: 1px solid #f76c06;
}

.catalog:before {
  top: -1px;
}

.catalog:after {
  bottom: -1px;
}

@media only screen and (min-width: 500px) {
  .catalog {
    display: flex;
    align-items: center;
    max-width: 210px;
    width: 100%;
  }
  .catalog p {
    font-size: 1rem;
    display: block;
    padding-left: 14px;
    text-transform: uppercase;
  }
}

@media only screen and (min-width: 1224px) {
  .catalog {
    max-width: 350px;
  }
  .catalog p {
    font-size: 1.11111rem;
  }
}

.catalog-btn {
  font-size: 0rem;
  padding: 10px;
  width: 53px;
}

.catalog-btn span {
  display: block;
  height: 3px;
  margin-bottom: 5px;
  background: #fff;
}

.catalog-btn span:last-child {
  margin-bottom: 0;
}

@media only screen and (min-width: 500px) {
  .catalog-btn {
    position: relative;
  }
  .catalog-btn:after {
    content: '';
    width: 1px;
    height: 104%;
    background: #c45300;
    position: absolute;
    top: -1px;
    right: -1px;
    z-index: 1;
  }
}

@media only screen and (min-width: 1224px) {
  .catalog-btn {
    padding: 19px 12px;
  }
}

.catalog-lst {
  position: absolute;
  min-width: 280px;
  top: 39px;
  padding: 15px;
  border-top: 1px solid #c45300;
  background: #f76c06;
  transition: .2s ease;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}

.catalog-lst li {
  position: relative;
  padding-right: 15px;
}

.catalog-lst li:after {
  content: "";
  position: absolute;
  top: 5px;
  right: 2px;
  width: 10px;
  height: 10px;
  border-right: 1px solid;
  border-bottom: 1px solid;
  transform: rotate(-45deg);
  border-color: inherit;
}

@media only screen and (min-width: 1224px) {
  .catalog-lst {
    top: 57px;
    min-width: 350px;
  }
}

.header-search {
  font-size: 1rem;
  font-weight: 300;
  background: url("../img/search.png") no-repeat 98.5%;
  background-size: 25px;
  width: 100%;
  border: 0;
  padding: 0 35px 0 20px;
  color: #383838;
  outline: none;
}

@media only screen and (min-width: 500px) {
  .header-search {
    padding-right: 55px;
  }
}

@media only screen and (min-width: 1224px) {
  .header-search {
    background-size: 30px;
  }
}

.open {
  opacity: 1;
  visibility: visible;
}

.catalog-btn.close-burger span,
.mob-burger-menu.close-burger span {
  position: relative;
}

.catalog-btn.close-burger span:first-child,
.mob-burger-menu.close-burger span:first-child {
  transform: rotate(45deg);
}

.catalog-btn.close-burger span:nth-child(2),
.mob-burger-menu.close-burger span:nth-child(2) {
  background: transparent;
}

.catalog-btn.close-burger span:last-child,
.mob-burger-menu.close-burger span:last-child {
  transform: rotate(-45deg);
}

.catalog-btn.close-burger span:first-child {
  top: 8px;
}

.catalog-btn.close-burger span:last-child {
  top: -8px;
}

.mob-burger-menu.close-burger span:first-child {
  top: 7px;
}

.mob-burger-menu.close-burger span:last-child {
  top: -7px;
}
