//General
.phone-list-title,
.address-list-title,
.call-back,
.catalog-btn {
  &:hover {
    cursor: pointer;
  }
}

.wrap:first-child p,
.info,
.cart p,
.catalog p {
  display: none;
}

// Top item
.header-item {
  border-bottom: 1px solid #bdbdbd;

  .container {
    display: flex;
    justify-content: space-between;
  }

  &:first-child {
    @include font-size(18);

    .container {
      padding-top: 14px;
      padding-bottom: 9px;
    }

    .auth {
      position: relative;
      padding-left: 24px;

      &:before {
        content: url("../img/login.png");
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
}

.mob-burger-menu {
  @include font-size(0);
  position: relative;
  width: 25px;

  span {
    width: 100%;
    height: 2px;
    background: $dark-txt;
    display: block;
    margin-bottom: 5px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @include media(min, 1024) {
    display: none;
  }
}

.navigation {
  position: absolute;
  top: 40px;
  left: 0;
  padding: 20px;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 2;
  visibility: hidden;
  opacity: 0;

  li {
    margin-bottom: 15px;
    padding-left: 12px;
    position: relative;

    &:last-child {
      margin-bottom: 0;
    }

    &:before {
      content: "";
      position: absolute;
      top: 3px;
      left: -4px;
      width: 8px;
      height: 8px;
      border-right: 1px solid $main-green;
      border-bottom: 1px solid $main-green;
      transform: rotate(-45deg);
    }
  }

  @include media(min, 1024) {
    display: flex;
    position: static;
    padding: 0;
    width: auto;
    visibility: visible;
    opacity: 1;

    li {
      margin-bottom: 0;
      margin-right: 35px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @include media(min, 1224) {
    li {
      &:before {
        top: 5px;
        width: 10px;
        height: 10px;
      }
    }
  }
}

// Middle item
.wrap {
  @include media(min, 500) {
    display: flex;
    align-items: center;
  }

  @include media(min, 1024) {
    &:first-child {
      p {
        @include font-size(14);
        display: block;
        padding: 9px 13px;
        max-width: 206px;
        border-left: 1px solid #bdbdbd;
      }
    }
  }
}

.logo {
  margin: 5px 0 5px -5px;
  max-width: 140px;

  @include media(min, 768) {
    max-width: 215px;
  }

  @include media(min, 1224) {
    max-width: none;
    margin: 16px 0 16px -10px;
  }
}

.info {
  @include media(min, 500) {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 10px 0;
    padding-right: 15px;
  }

  @include media(min, 768) {
    padding-right: 50px;
  }
}

.work-time {
  @include font-size(12);
  color: $light-txt;

  &:before {
    content: url("../img/clock.png");
    margin-right: 4px;
    position: relative;
    top: 2px;
  }
}

.phone-link {
  @include font-size(24);
  display: inline-block;
  margin: 5px 0;
  color: $txt-green;

  &:hover {
    text-decoration: none;
  }
}

.phone-list-title,
.address-list-title {
  &:after {
    content: "";
    position: absolute;
    right: -16px;
    width: 8px;
    height: 8px;
    border-right: 1px solid #8ecf04;
    border-bottom: 1px solid #8ecf04;
    transform: rotate(45deg);
  }
}

.phone-list-title {
  @include font-size(14);
  position: relative;
  margin-right: 18px;
  color: $light-txt;
  border-bottom: 1px dashed;

  .info-list {
    @include font-size(20);
    min-width: 160px;
    text-align: center;
    color: $txt-green;
  }

  @include media(min, 1224) {
    &:after {
      top: 3px;
    }
  }
}

.info-list {
  padding: 10px;
  min-width: 220px;
  position: absolute;
  top: 20px;
  right: -20px;
  background: #fff;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, .5);
  transition: .2s ease;
  opacity: 0;
  z-index: 1;
  visibility: hidden;
}

.info-address {
  @include font-size(16);
  font-weight: $light;
  color: $light-txt;
}

.address-list-title,
.call-back {
  position: relative;
  border-bottom: 1px dashed;
  margin-left: 18px;

  &:before {
    content: '';
    position: absolute;
    left: -16px;
    top: -2px;
  }
}

.address-list-title {
  margin-bottom: 8px;
  margin-right: 20px;

  &:before {
    content: url("../img/map-marker.png");
  }

  &:after {
    top: 2px;
  }

  @include media(min, 1224) {
    &:after {
      top: 4px;
    }
  }
}

.call-back {
  &:before {
    content: url(../img/phone.png);
    top: 2px;
  }
}

.cart {
  @include font-size(16);
  position: relative;
  height: 100%;
  color: $bg-txt;
  background: url("../img/cart.png") no-repeat 35% 50% $main-green;
  background-size: 80% 60%;
  padding-left: 50px;

  span {
    @include font-size(17);
    position: absolute;
    top: 6px;
    left: 25px;
  }

  .price {
    @include font-size(12);
    font-weight: $light;
    margin-top: 5px;
  }

  @include media(min, 500) {
    height: 70%;
    background-size: 80% 50%;

    span {
      top: 10px;
    }
  }

  @include media(min, 768) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-right: 15px;
    padding-left: 80px;
    height: 100%;
    background-position: 15% 50%;
    background-size: 35% 40%;

    span {
      top: 20px;
      left: 42px;
    }

    p {
      display: block;
    }
  }

  @include media(min, 1224) {
    span {
      top: 30px;
      left: 44px;
    }
  }
}

// Bottom item
.catalog {
  position: relative;
  background: $btn-color;
  color: $bg-txt;

  &:before,
  &:after {
    content: '';
    width: 100%;
    position: absolute;
    border-top: 1px solid $btn-color;
  }

  &:before {
    top: -1px;
  }

  &:after {
    bottom: -1px;
  }

  @include media(min, 500) {
    display: flex;
    align-items: center;
    max-width: 210px;
    width: 100%;

    p {
      @include font-size(18);
      display: block;
      padding-left: 14px;
      text-transform: uppercase;
    }
  }

  @include media(min, 1224) {
    max-width: 350px;

    p {
      @include font-size(20);
    }
  }
}

.catalog-btn {
  @include font-size(0);
  padding: 10px;
  width: 53px;

   span {
     display: block;
     height: 3px;
     margin-bottom: 5px;
     background: $bg-txt;

     &:last-child {
       margin-bottom: 0;
     }
   }

  @include media(min, 500) {
    position: relative;

    &:after {
      content: '';
      width: 1px;
      height: 104%;
      background: #c45300;
      position: absolute;
      top: -1px;
      right: -1px;
      z-index: 1;
    }
  }

  @include media(min, 1224) {
    padding: 19px 12px;
  }
}

.catalog-lst {
  position: absolute;
  min-width: 280px;
  top: 39px;
  padding: 15px;
  border-top: 1px solid #c45300;
  background: #f76c06;
  transition: .2s ease;
  z-index: 1;
  opacity: 0;
  visibility: hidden;

  li {
    position: relative;
    padding-right: 15px;

    &:after {
      content: "";
      position: absolute;
      top: 5px;
      right: 2px;
      width: 10px;
      height: 10px;
      border-right: 1px solid;
      border-bottom: 1px solid;
      transform: rotate(-45deg);
      border-color: inherit;
    }
  }

  @include media(min, 1224) {
    top: 57px;
    min-width: 350px;
  }
}

.header-search {
  @include font-size(18);
  font-weight: $light;
  background: url("../img/search.png") no-repeat 98.5%;
  background-size: 25px;
  width: 100%;
  border: 0;
  padding: 0 35px 0 20px;
  color: $dark-txt;
  outline: none;

  @include media(min, 500) {
    padding-right: 55px;
  }

  @include media(min, 1224) {
    background-size: 30px;
  }
}

.open {
  opacity: 1;
  visibility: visible;
}

.catalog-btn.close-burger,
.mob-burger-menu.close-burger {
  span {
    position: relative;

    &:first-child {
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      background: transparent;
    }

    &:last-child {
      transform: rotate(-45deg);
    }
  }
}

.catalog-btn.close-burger {
  span {
    &:first-child {
      top: 8px;
    }

    &:last-child {
      top: -8px;
    }
  }
}

.mob-burger-menu.close-burger {
  span {
    &:first-child {
      top: 7px;
    }

    &:last-child {
      top: -7px;
    }
  }
}
